export const checkDKS = (barcode) => {
  console.log(barcode.substr(5, 1));
  if (barcode.substr(5, 1) === "#") {
    let NW = 0;
    let GW = 0;
    let PKG = 0;

    GW = parseFloat(barcode.split("#")[0]).toFixed(3) / 100;
    NW = parseFloat(barcode.split("#")[1]).toFixed(3) / 100;
    PKG = GW - NW;

    console.log(barcode);
    let data = {
      NW: NW,
      GW: GW,
      PKG: PKG,
    };
    return data;
  } else {
    return false;
  }
};
