import React, { Component } from "react";
import { Field, reduxForm, formValueSelector } from "redux-form";
import { connect } from "react-redux";
import validationTerimaSupplier from "../../Validations/validationTerimaSupplier";
import { getTimbangan } from "../../GetTimbangan";
import { renderField, renderFieldSelect } from "../../renderField";
import { AxiosMasterGet } from "../../Axios";
import { SkeletonLoading } from "../../helper";
import { CheckSupplier } from "./CheckSupplier";

class FormPengambilanBarcode extends Component {
  constructor(props) {
    super(props);
    this.onclick1 = this.onclick1.bind(this);
    this.state = {
      kodeBarang: [],
      DataSupplier: [],
      kode_barang: "",
      keterangan: "INI KETERANGAN",
      kadar: "",
      P: "",
      K: "",
      bungkus: "",
      berat: "",
      harga: "",
      murni: "",
      ongkos: "",
      status: "",
      kadarCetak: "",
      kadarBeli: "",
      namaJenis: "",
      kodeJenis: "",
      text: "",
    };
    this.timer = null;
  }

  componentDidMount() {
    AxiosMasterGet("jenis/get/by-kode-kelompok/PERHIASAN")
      .then((res) =>
        this.setState({
          kodeBarang: res.data,
        })
      )
      .catch((err) => console.log(err));
    getTimbangan(this.props.change);
  }

  onclick1() {
    this.props.change("netto", this.props.netto);
    this.props.change("noFaktur", this.props.noFaktur);
    this.props.change("pkg", this.props.pkg);
  }

  pilihBarang(data) {
    var supp = data ? data : "DEFAULT | DEFAULT | DEFAULT | DEFAULT ";
    var result_explode = supp.split("|");
    this.setState({
      kodeJenis: result_explode[0],
      namaJenis: result_explode[1],
      kadarCetak: result_explode[2],
      kadarJual: result_explode[3],
      kadarBeli: result_explode[4],
      barang: "terpilih",
    });
  }

  setKadar() {
    this.props.change("keterangan", this.state.namaJenis);
    this.props.change("kadar", this.state.kadarCetak);
    if (
      localStorage.getItem("jenisTransaksi") === "RETUR SUPPLIER" ||
      localStorage.getItem("jenisTransaksi") === "PENERIMAAN SUPPLIER"
    ) {
      this.props.change("kadarTransaksi", this.state.kadarBeli);
    } else {
      this.props.change("kadarTransaksi", this.state.kadarJual);
    }
  }

  onChange = (e) => {
    this.setState({
      text: e.target.value,
    });
  };
  componentDidUpdate(prevProps, prevState) {
    if (prevState.text !== this.state.text) {
      this.handleCheck();
    }
  }
  handleCheck = () => {
    // Clears running timer and starts a new one each time the user types
    clearTimeout(this.timer);
    this.timer = setTimeout(() => {
      let akhir = CheckSupplier(this.state.text);
      if (akhir) {
        console.log(`GW : ${akhir.GW} - NW : ${akhir.NW} - PKG : ${akhir.PKG}`);
        this.props.change("bruto", akhir.GW - akhir.PKG);
        this.props.change("gross", akhir.GW);
        this.props.change("netto", this.props.netto);
        this.props.change("noFaktur", this.props.noFaktur);
        this.props.change("pkg", this.props.pkg);
      }
      this.onclick1();
      document.getElementById("simpan").click();
      this.props.change("barcode", null);
    }, 150);
  };

  render() {
    return (
      <form onSubmit={this.props.handleSubmit}>
        <div className="row">
          <div className="col-md-12 col-lg-12 d-none">
            <Field
              name="noFaktur"
              label={this.props.noFaktur}
              type="text"
              readOnly={true}
              component={renderField}
              defaultValue={this.props.noFaktur}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div>
          <div className="col-md-4 form-group">
            {String(this.state.kodeBarang) !== "" ? (
              <Field
                label="Kode Model"
                name="kodeJenis"
                component={renderFieldSelect}
                options={this.state.kodeBarang.map((list) => {
                  let data = {
                    value:
                      list.kode_jenis +
                      "|" +
                      list.nama_jenis +
                      "|" +
                      list.kadar_cetak +
                      "|" +
                      list.kadar_jual +
                      "|" +
                      list.kadar_beli,
                    name: list.nama_jenis,
                  };
                  return data;
                })}
                onChange={(data) => this.pilihBarang(data)}
                onBlur={this.setKadar()}
                placeholder="Silahkan Pilih"
              ></Field>
            ) : (
              <SkeletonLoading label="Kode Model" />
            )}
          </div>
          {/* <div className="col-md-8 form-group">
            <Field
              name="keterangan"
              label="Keterangan"
              type="text"
              component={renderField}
              normalize={(value) => value && value.toUpperCase()}
            />
          </div> */}
          <div className="col-lg-8">
            <Field
              name="barcode"
              component={renderField}
              type="text"
              label="Barcode"
              readOnly={this.state.barang === undefined ? true : false}
              placeholder="Masukan Barcode"
              onChange={this.onChange}
            />
          </div>
          <div className="col-lg-12">
            <p>*Barang Akan Bertambah Otomatis Setelah Scan Barcode</p>
          </div>
          <div className="col-md-12 form-group mt-3 d-none">
            <div className="text-right">
              <button
                className="btn btn-primary"
                type="submit"
                id="simpan"
                disabled={this.props.submitting}
              >
                Simpan
              </button>
            </div>
          </div>
        </div>
      </form>
    );
  }
}

FormPengambilanBarcode = reduxForm({
  form: "TerimaSupplier",
  validate: validationTerimaSupplier,
  enableReinitialize: true,
})(FormPengambilanBarcode);
const selector = formValueSelector("TerimaSupplier"); // <-- same as form name

export default connect((state) => {
  const { bruto, kadarTransaksi, gross } = selector(
    state,
    "bruto",
    "kadarTransaksi",
    "gross"
  );
  return {
    netto: (parseFloat(bruto) * (parseFloat(kadarTransaksi) / 100)).toFixed(3),
    pkg: (parseFloat(gross) - parseFloat(bruto)).toFixed(3),
  };
})(FormPengambilanBarcode);
