import { checkDKS } from "../BarcodeSupplier/DKS";
import { checkHWT } from "../BarcodeSupplier/HWT";
import { checkINTERN } from "../BarcodeSupplier/INTERN";
import { checkLLP } from "../BarcodeSupplier/LLP";
import { checkMT } from "../BarcodeSupplier/MT";
import { checkUBS } from "../BarcodeSupplier/UBS";

export const CheckSupplier = (barcode) => {
  //   console.log(barcode);
  let UBS = checkUBS(barcode);
  let INTERN = checkINTERN(barcode);
  let DKS = checkDKS(barcode);
  let LLP = checkLLP(barcode);
  let HWT = checkHWT(barcode);
  let MT = checkMT(barcode);
  if (UBS) {
    return UBS;
  } else if (INTERN) {
    return INTERN;
  } else if (DKS) {
    return DKS;
  } else if (LLP) {
    return LLP;
  } else if (HWT) {
    return HWT;
  } else if (MT) {
    return MT;
  }
};
