import React, { Component } from "react";
import FormPengambilanBarcode from "./Component/FormPengambilanBarcode";
import SelectSearch from "react-select-search";
import Swal from "sweetalert2";
import { getListDataPengambilanBarcode } from "../actions/pengambilanBarcodeAction";
import { connect } from "react-redux";
import { Field, reduxForm } from "redux-form";
import HeaderCard from "../HeaderCard";
import { getUUID } from "../actions/fakturAction";
import { getcustomerList } from "../actions/customerAction";
import { getLokasiPindah, getsupplierList } from "../actions/supplierAction";
import { AxiosMasterPost } from "../Axios";
import { berhasil, gagal, warning } from "../Alert";
import { multipleDeleteLocal } from "../helper";
import GlobalTabel from "../GlobalComponent/GlobalTabel";
import { finishSend, progressSend } from "../actions/LogicAction";
import { renderField, renderFieldSelect } from "../renderField";
import { ToastSucces } from "../GlobalComponent/notification";
import { getToday } from "../GlobalComponent/function";

const maptostate = (state) => {
  return {
    pengambilanBarcode: state.provinsi.pengambilanBarcode,
    noFaktur: state.provinsi.noFaktur,
    customer: state.provinsi.customer,
    supplier: state.provinsi.supplier,
    lokasi: state.provinsi.dataLokasiPindah,
    onSend: state.provinsi.onSend,
    initialValues: {
      tanggal: getToday(),
    },
  };
};
class PengambilanBarcod extends Component {
  constructor(props) {
    super(props);
    this.state = {
      jenisTransaksi: [
        {
          value: "RETUR SUPPLIER",
          name: "RETUR SUPPLIER",
        },
        {
          value: "PENERIMAAN SUPPLIER",
          name: "PENERIMAAN SUPPLIER",
        },
        {
          value: "PINDAH BARANG",
          name: "PINDAH BARANG",
        },
        {
          value: "PENJUALAN",
          name: "PENJUALAN",
        },
        {
          value: "RETUR CUSTOMER",
          name: "RETUR CUSTOMER",
        },
      ],
      columns: [
        {
          dataField: "kode_jenis",
          text: "Kode Model",
        },
        {
          dataField: "kode_barcode",
          text: "Barcode",
        },
        {
          dataField: "gross",
          text: "Gross",
        },
        {
          dataField: "pkg",
          text: "Bungkus\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "bruto",
          text: "Berat\n(Gr)",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "kadar_cetak",
          text: "Cap Kode",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "kadar",
          text: "Kode Tukar",
          align: "right",
          headerAlign: "right",
        },
        {
          dataField: "netto",
          text: "Murni\n(Gr)",
          align: "right",
          headerAlign: "right",
          footerClasses: "sum-netto-terima-supplier",
        },
        {
          dataField: "ongkos_rp",
          text: "Ongkos\n(RP)",
        },
        {
          dataField: "Aksi",
          text: "Aksi",
          csvExport: false,
          formatter: (rowContent, row, index) => {
            return (
              <div className="text-center">
                <button
                  className="btn btn-danger mr-2"
                  data-tut="reactour_4"
                  onClick={() => this.hapus_barcode(index)}
                >
                  <i className="fa fa-trash mr-1"></i>
                  HAPUS
                </button>
              </div>
            );
          },
        },
      ],
      kode_supplier: "",
      kode_customer: "",
      kode_lokasi_asal: "",
      kode_lokasi_tujuan: "",
      jenisTransaksiSelected: "",
      tanggal: "",
    };
  }

  hapus_barcode(index) {
    Swal.fire({
      title: "Anda Yakin ?",
      text: "Anda Akan Menghapus Data Ini ?",
      icon: "warning",
      showCancelButton: true,
      confirmButtonColor: "#3085d6",
      cancelButtonColor: "#d33",
      confirmButtonText: "Yes, delete it!",
    })
      .then((result) => {
        if (result.isConfirmed) {
          let data = JSON.parse(
            localStorage.getItem("pengambilanBarcode_temp")
          );
          data.splice(index, 1);
          localStorage.setItem("pengambilanBarcode_temp", JSON.stringify(data));
        }
      })
      .then(() => this.props.dispatch(getListDataPengambilanBarcode()));
  }

  inputChange = (input) => (e) => {
    this.setState({
      [input]: e.target.value,
    });
  };

  componentDidMount() {
    this.props.dispatch(getListDataPengambilanBarcode());
    this.props.dispatch(getUUID());
    this.props.dispatch(getcustomerList());
    this.props.dispatch(getsupplierList());
    this.props.dispatch(getLokasiPindah());
    localStorage.removeItem("jenisTransaksi");
  }
  paddingNumberGW(str) {
    var pad = "00000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  paddingNumberNW(str) {
    var pad = "000000";
    var ans = pad.substring(0, pad.length - str.length) + str;
    return ans.replace(".", "");
  }
  handleSubmit(data) {
    var supp = data.kodeJenis
      ? data.kodeJenis
      : "DEFAULT|DEFAULT|DEFAULT|DEFAULT";
    var split = supp.split("|");
    var gross = parseFloat(data.bruto) + parseFloat(data.pkg);
    var barcode = `${split[0]}X>C${this.paddingNumberGW(
      parseFloat(gross).toFixed(2),
      7
    )}${this.paddingNumberNW(parseFloat(data.netto).toFixed(2), 5)}`;
    var array =
      JSON.parse(localStorage.getItem("pengambilanBarcode_temp")) || [];
    var simpan = {
      kode_jenis: split[0],
      nama_jenis: data.keterangan,
      kadar_cetak: data.kadar.toString(),
      kadar: data.kadarTransaksi,
      kode_barcode: barcode,
      pkg: data.pkg,
      gross: data.gross,
      bruto: data.bruto,
      netto: data.netto,
      ongkos_rp: data.rp || 0,
    };
    let posisi = array.findIndex((data) => data.kode_barcode === barcode);
    if (posisi < 0) {
      array.push(simpan);
      localStorage.setItem("pengambilanBarcode_temp", JSON.stringify(array));
      ToastSucces("Berhasil Menambahkan Data");
      this.props.dispatch(getListDataPengambilanBarcode());
      // this.props.dispatch(reset("TerimaSupplier"));
    } else {
      warning(`Barcode Sudah Ada DI Tabel Barang`);
      return false;
    }
  }

  pengambilanBarcodeSubmit() {
    if (this.state.jenisTransaksiSelected === "") {
      warning("Harap Pilih Jenis Transaksi");
      return false;
    } else if (this.state.tanggal === "") {
      warning("Harap Pilih Tanggal");
      return false;
    }
    var instansi = "";
    var instansi2 = "";
    if (this.state.jenisTransaksiSelected === "PENJUALAN") {
      instansi = this.state.kode_customer;
      instansi2 = this.state.kode_customer;
    } else if (this.state.jenisTransaksiSelected === "RETUR SUPPLIER") {
      instansi = this.state.kode_supplier;
      instansi2 = this.state.kode_supplier;
    } else if (this.state.jenisTransaksiSelected === "PENERIMAAN SUPPLIER") {
      instansi = this.state.kode_supplier;
      instansi2 = this.state.kode_supplier;
    } else if (this.state.jenisTransaksiSelected === "PINDAH BARANG") {
      instansi = this.state.kode_lokasi_asal;
      instansi2 = this.state.kode_lokasi_tujuan;
    } else if (this.state.jenisTransaksiSelected === "RETUR CUSTOMER") {
      instansi = this.state.kode_customer;
      instansi2 = this.state.kode_customer;
    }
    var local = JSON.parse(localStorage.getItem("pengambilanBarcode_temp"));
    var qty = local.length;
    var bruto = local
      .map((list) => parseFloat(list.bruto))
      .reduce((a, b) => a + b, 0);
    var pkg = local
      .map((list) => parseFloat(list.pkg))
      .reduce((a, b) => a + b, 0);
    var gross = local
      .map((list) => parseFloat(list.gross))
      .reduce((a, b) => a + b, 0);
    var netto = local
      .map((list) => parseFloat(list.netto))
      .reduce((a, b) => a + b, 0);
    var ongkos_rp = local
      .map((list) => parseFloat(list.ongkos_rp))
      .reduce((a, b) => a + b, 0);
    this.props.dispatch(progressSend());
    var simpan = {
      no_barcode: this.props.noFaktur,
      jenis_trx: this.state.jenisTransaksiSelected,
      tanggal: this.state.tanggal || getToday(),
      baki: "-",
      instansi: instansi,
      instansi2: instansi2,
      total_qty: qty,
      total_bruto: bruto,
      total_pkg: pkg,
      total_gross: gross,
      total_netto: netto,
      total_ongkos: ongkos_rp,
      detail_barang: JSON.parse(
        localStorage.getItem("pengambilanBarcode_temp")
      ),
    };
    AxiosMasterPost("pengambilan-barcode/simpan", simpan)
      .then(() => berhasil("Simpan Data Berhasil"))
      .then(() => multipleDeleteLocal(["pengambilanBarcode_temp", "noFaktur"]))
      .then(() => this.props.dispatch(getListDataPengambilanBarcode()))
      .then(() => this.props.dispatch(getUUID()))
      .then(() => this.props.dispatch(finishSend()))
      .then(() => window.location.reload())
      .catch((err) =>
        gagal(`Gagal Simpan Data , Detail : ${err.response.data}`).then(() =>
          this.props.dispatch(finishSend())
        )
      );
  }

  setJenisTransaksi(e) {
    this.setState({
      jenisTransaksiSelected: e,
    });
    localStorage.setItem("jenisTransaksi", e);
  }
  render() {
    return (
      <div className="container-fluid" style={{ color: "black" }}>
        <div className="row">
          <div className="col-12">
            <div className="card">
              <HeaderCard title="Rekam Barang Barcode" />
              <div className="card-body">
                <div className="col-lg-12 col-md-12">
                  <div className="row">
                    <div className="col-lg-4">
                      <Field
                        name="jenisTransaksi"
                        component={renderFieldSelect}
                        options={this.state.jenisTransaksi}
                        type="text"
                        label="Jenis Transaksi"
                        placeholder="Masukan Jenis Transaksi"
                        onChange={(data) => this.setJenisTransaksi(data)}
                      />
                    </div>
                    <div className="col-md-4 col-lg-4">
                      <Field
                        type="date"
                        label="Tanggal"
                        name="tanggal"
                        component={renderField}
                        className="form-control"
                        onChange={this.inputChange("tanggal")}
                      />
                    </div>
                    {/* <div className="col-md-4 col-lg-4">
                      <label htmlFor="">Baki</label>
                      <input
                        type="text"
                        id="baki"
                        name="baki"
                        className="form-control"
                        onChange={this.inputChange("baki")}
                      />
                    </div> */}
                    {this.state.jenisTransaksiSelected === "PENJUALAN" ? (
                      <>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.props.customer.map((list) => {
                              let data = {
                                value: list.kode_customer,
                                name: list.nama_customer,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_customer: data,
                              })
                            }
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksiSelected ===
                      "PENERIMAAN SUPPLIER" ? (
                      <>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Nama Supplier</label>
                          <SelectSearch
                            options={this.props.supplier.map((list) => {
                              let data = {
                                value: list.kode_supplier,
                                name: list.nama_supplier,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_supplier: data,
                              })
                            }
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksiSelected ===
                      "RETUR SUPPLIER" ? (
                      <>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Nama Supplier</label>
                          <SelectSearch
                            options={this.props.supplier.map((list) => {
                              let data = {
                                value: list.kode_supplier,
                                name: list.nama_supplier,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_supplier: data,
                              })
                            }
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksiSelected ===
                      "PINDAH BARANG" ? (
                      <>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Dari</label>
                          <SelectSearch
                            options={this.props.lokasi.map((list) => {
                              let data = {
                                value: list.kode_lokasi,
                                name: list.nama_lokasi,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_lokasi_asal: data,
                              })
                            }
                          />
                        </div>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Ke</label>
                          <SelectSearch
                            options={this.props.lokasi.map((list) => {
                              let data = {
                                value: list.kode_lokasi,
                                name: list.nama_lokasi,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_lokasi_tujuan: data,
                              })
                            }
                          />
                        </div>
                      </>
                    ) : this.state.jenisTransaksiSelected ===
                      "RETUR CUSTOMER" ? (
                      <>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Nama Toko</label>
                          <SelectSearch
                            options={this.props.customer.map((list) => {
                              let data = {
                                value: list.kode_customer,
                                name: list.nama_customer,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_customer: data,
                              })
                            }
                          />
                        </div>
                      </>
                    ) : (
                      <>
                        <div className="col-md-5 col-lg-4 ">
                          <label htmlFor="">Nama Supplier</label>
                          <SelectSearch
                            options={this.props.supplier.map((list) => {
                              let data = {
                                value: list.kode_supplier,
                                name: list.nama_supplier,
                              };
                              return data;
                            })}
                            search
                            placeholder="Silahkan Pilih"
                            onChange={(data) =>
                              this.setState({
                                kode_supplier: data,
                              })
                            }
                          />
                        </div>
                      </>
                    )}
                    <div className="col-lg-12 col-md-12 mt-4">
                      <FormPengambilanBarcode
                        onSubmit={(data) => this.handleSubmit(data)}
                        noFaktur={this.props.noFaktur}
                      />
                    </div>
                    <div className="col-lg-12">
                      <GlobalTabel
                        keyField="kodeJenis"
                        data={this.props.pengambilanBarcode || []}
                        columns={this.state.columns}
                        textEmpty="Silahkan Tambah Data Dengan Klik Tombol Biru Diatas"
                      />
                    </div>
                    <div className="col-lg-12">
                      <div className="text-right">
                        <button
                          className="btn btn-primary"
                          onClick={() => this.pengambilanBarcodeSubmit()}
                          disabled={this.props.onSend}
                        >
                          SIMPAN
                        </button>
                      </div>
                    </div>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
        <div
          id="primary-header-modal"
          className="modal fade"
          tabIndex="-1"
          role="dialog"
          aria-labelledby="primary-header-modalLabel"
          aria-hidden="true"
        >
          <div className="modal-dialog modal-lg">
            <div className="modal-content">
              <div className="modal-header modal-colored-header bg-primary">
                <h4 className="modal-title" id="primary-header-modalLabel">
                  Tambah Data Rekam Barang Barcode
                </h4>
                <button
                  type="button"
                  id="dismissModal"
                  className="close"
                  data-dismiss="modal"
                  aria-hidden="true"
                >
                  
                </button>
              </div>
              <div className="modal-body"></div>
            </div>
          </div>
        </div>
      </div>
    );
  }
}

PengambilanBarcod = reduxForm({
  form: "PengambilanBarcod",
  enableReinitialize: true,
})(PengambilanBarcod);
export default connect(maptostate, null)(PengambilanBarcod);
