export const checkMT = (barcode) => {
  if (
    barcode.indexOf(" ") > 0 ||
    barcode.indexOf("-") > 0 ||
    barcode.indexOf("//") > 0 ||
    barcode.indexOf("/") > 0
  ) {
    let GW = 0;
    let NW = 0;
    let PKG = 0;
    // BARCODE YT
    if (barcode.indexOf("-") > 0) {
      GW = parseFloat(barcode.split("-")[0]).toFixed(2);
      NW = parseFloat(barcode.split("-")[1]).toFixed(2);
      PKG = parseFloat(GW) - parseFloat(NW);
      let data = {
        NW: NW,
        GW: GW,
        PKG: PKG,
      };
      return data;
    }

    if (barcode.indexOf("//") > 0) {
      inMT(barcode);
    } else {
      return false;
    }
  } else {
    return false;
  }
};

function inMT(Data) {
  let GW = 0;
  let NW = 0;
  let PKG = 0;
  if (Data.indexOf("//") > 0) {
    let StringArray = Data.split("//");
    inNormal(StringArray);
  } else {
    if (Data.indexOf("/") > 0) {
      let StringArray = Data.split("/");
      inNormal(StringArray);
    } else {
      let StringArray = Data.split(" ");
      GW = parseFloat(StringArray[0]).toFixed(2);
      NW = parseFloat(StringArray[1]).toFixed(2);
      PKG = parseFloat(GW) - parseFloat(NW);
      let data = {
        GW: GW,
        NW: NW,
        PKG: PKG,
      };
      return data;
    }
  }
}

function inNormal(Data) {
  let GW = 0;
  let NW = 0;
  let PKG = 0;
  GW = parseFloat(Data[0].trim()).toFixed(2);
  NW = parseFloat(Data[1].trim()).toFixed(2);
  PKG = parseFloat(GW) - parseFloat(NW);
  let data = {
    NW: NW,
    GW: GW,
    PKG: PKG,
  };
  return data;
}
