export const checkHWT = (barcode) => {
  let GW = "";
  let NW = "";
  let PKG = "";
  if (
    barcode.indexOf(" ") === -1 &&
    barcode.indexOf("-") === -1 &&
    barcode.indexOf("//") === -1 &&
    barcode.indexOf("/") === -1
  ) {
    if (
      (barcode.length === 14 &&
        barcode.substr(0, 1) !== "1" &&
        barcode.substr(0, 1) === barcode.substr(5, 1)) ||
      barcode.length === 15 ||
      barcode.length === 16 ||
      barcode.length === 17 ||
      barcode.length === 18
    ) {
      //    SUPPLIER LI
      if (barcode.length === 14) {
        GW = parseFloat(barcode.substr(1, 4)) / 1000;
        NW = parseFloat(barcode.substr(6, 4)) / 1000;
        PKG = parseFloat(GW) - parseFloat(NW);

        // Untuk kasus barcode HWT Baru
        if (parseFloat(NW).toFixed(3) === "0.000") {
          if (barcode.length === 20) {
            GW =
              parseFloat(
                barcode.substring(barcode.length - 1, barcode.length - 6)
              ).toFixed(2) / 100;
            NW = parseFloat(barcode.substr(8, 6)).toFixed(2) / 100;
            PKG = parseFloat(GW) - parseFloat(NW);
          } else {
            GW =
              parseFloat(
                barcode.substring(barcode.length - 1, barcode.length - 9)
              ).toFixed(2) / 100;
            NW =
              parseFloat(barcode.substr(0, barcode.length - 9)).toFixed(2) /
              100;
            PKG = parseFloat(GW) - parseFloat(NW);
          }
          let data = {
            NW: NW,
            PKG: PKG,
            GW: GW,
          };
          return data;
        }
      } else if (barcode.length === 16) {
        GW = parseFloat(barcode.substr(2, 5)) / 1000;
        NW = parseFloat(barcode.substr(8, 5)) / 1000;
        PKG = parseFloat(GW) - parseFloat(NW);
      }
      let data = {
        NW: NW,
        PKG: PKG,
        GW: GW,
      };
      return data;
    } else if (
      barcode.length === 12 ||
      barcode.length === 13 ||
      barcode.length === 20
    ) {
      // SUPPLIER HWT
      if (barcode.length === 20) {
        GW =
          parseFloat(
            barcode.substring(barcode.length - 1, barcode.length - 6)
          ).toFixed(2) / 100;
        NW = parseFloat(barcode.substr(9, 6)).toFixed(2) / 100;
        PKG = parseFloat(GW) - parseFloat(NW);
      } else {
        GW =
          parseFloat(
            barcode.substring(barcode.length - 1, barcode.length - 9)
          ).toFixed(2) / 100;
        NW = parseFloat(barcode.substr(0, barcode.length - 9)).toFixed(2) / 100;
        PKG = parseFloat(GW) - parseFloat(NW);
      }
      let data = {
        NW: NW,
        PKG: PKG,
        GW: GW,
      };
      return data;
    }
  }
  return false;
};
