export const checkUBS = (barcode) => {
  if (barcode.length === 22 && barcode.substr(0, 1) === "0") {
    console.log("INI DI UBS");
    console.log(barcode.substring(14, 5));
    let NW = parseFloat(barcode.substr(14, 4)) / 100;
    let PKG = parseFloat(barcode.substr(18, 4)) / 100;
    let GW = NW + PKG;

    let data = {
      NW: NW,
      PKG: PKG,
      GW: GW,
    };
    return data;
  } else {
    return false;
  }
};
