export const checkLLP = (barcode) => {
  if (barcode.substr(0, 3) === "LLP") {
    console.log("INI DI LLP");
    let hasil = barcode.split("-");
    let GW = hasil[1].substr(0, hasil[1].length - 1);
    let NW = hasil[2].substr(0, hasil[2].length - 1);
    let PKG = parseFloat(GW) - parseFloat(NW);
    let data = {
      NW: NW,
      PKG: PKG,
      GW: GW,
    };
    return data;
  } else {
    return false;
  }
};
