export const checkINTERN = (barcode) => {
  let awal = barcode.indexOf("X");
  let akhir = barcode.length - 1;
  let posisi = akhir - awal;
  if (posisi === 12 && barcode.substr(1, 1) !== "0") {
    console.log(barcode.substr(awal + 7, 5));
    let GW = parseFloat(barcode.substr(awal + 1, 7)) / 100;
    let PKG = parseFloat(barcode.substr(awal + 8, 5)) / 100;
    let NW = GW - PKG;

    let data = {
      NW: NW,
      PKG: PKG,
      GW: GW,
    };
    return data;
  } else {
    return false;
  }
};
